<template>
  <div class="practice_view">
    <head-top title="随堂练习"></head-top>
    <div class="main">
      <div class="content">
        <div class="tt">
          {{currentIndex + '.【' + getPracticeTypeName(practiceInfo['PSType']) + '】' + (practiceInfo['PSScore']||0) + '分'}}
        </div>

        <div class="ct">{{practiceInfo['PSContent']}}</div>
        <div v-if="practiceInfo['PSType']===1 || practiceInfo['PSType']===2 " class="ans">
          <div class="an" v-for="(item, k) in practiceOptions" :key="k">
            <span>{{ item['SOSerial']}}：</span>
            <span>{{item['SOContent']}}</span>
          </div>
        </div>
      </div>

      <my-answer :question-type="practiceInfo['PSType']" :my-answer="practiceAnswer"
        :module-picture="modulePicture" :true-answer="practiceInfo['PSAnswer']"
        :analysis="practiceInfo['PSanalysis']"
        :get-score="practiceReply? practiceReply['PAScore'] : 0">
      </my-answer>

      <!-- <div class="survey_info">
        <van-button v-if="surveyMaster===1" icon="success" type="primary" disabled>
          已掌握
        </van-button>
        <van-button v-else-if="surveyMaster===0" icon="cross" type="danger" disabled>
          未掌握
        </van-button>
        <van-button v-else type="warning" disabled>
          未参与调研
        </van-button>
      </div> -->

      <div class="footer">
        <button class="backTo" v-show="currentIndex> 1" @click="backTo">上一题</button>
        <button class="nextTo" v-if="currentIndex< total" @click="nextTo">下一题</button>
      </div>
    </div>
  </div>
</template>

<script>
import MyAnswer from "@/components/Question/MyAnswer";
import { practiceTypeName, ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: { MyAnswer },
  props: {},
  data() {
    return {
      taskId: this.$route.params.taskId,
      practiceId: this.$route.params.practiceId,
      libraryId: this.$route.params.libraryId,
      classHourId: this.$route.params.classHourId,

      practiceInfo: {},
      practiceOptions: [],
      practiceReply: {},
      total: parseInt(this.$route.params.total || "1"),
      practiceAnswer: "",
      modulePicture: ModulePicture.PractiseAnswer,
      surveyMaster: null,
      currentIndex: 1
    };
  },
  created() {
    this.getPracticeInfo();
  },
  methods: {
    getPracticeInfo() {
      this.$mainHttp.classPractise
        .getPracticeMental({ plID: this.libraryId + "" })
        .then(res => {
          if (res.Result) {
            this.total = res.Total;
            this.getDataInfo();
          } else {
          }
        })
        .catch(err => {});
    },
    getDataInfo() {
      Object.keys(this.practiceInfo).forEach(item => {
        this.$delete(this.practiceInfo, item);
      });
      Object.keys(this.practiceReply).forEach(item => {
        this.$delete(this.practiceReply, item);
      });
      this.practiceOptions = [];

      this.$mainHttp.classPractise
        .getData({
          cipID: this.taskId,
          // cpid: this.practiceId,
          plid: this.libraryId,
          psSerial: this.currentIndex,
          studentID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result) {
            this.practiceInfo = res.PSData;
            this.practiceReply = res.PAData ? res.PAData : {};
            this.practiceAnswer = this.practiceReply["PAContent"];

            if (this.practiceReply) {
              this.surveyMaster = this.practiceReply["PAMaster"] || null;
            }

            this.practiceOptions = [];
            if (res.SOData && res.SOData.length > 0) {
              res.SOData.forEach(item => {
                if (this.practiceOptions.indexOf(item) === -1)
                  this.practiceOptions.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getPracticeTypeName(val) {
      return practiceTypeName(val);
    },
    nextTo() {
      this.currentIndex += 1;
      this.getDataInfo();
    },
    backTo() {
      this.currentIndex -= 1;
      this.getDataInfo();
    },
    exit() {
      this.$router.push("/space/homework");
    }
  }
};
</script>

<style lang='less' scoped>
.practice_view {
  width: 100%;
  height: 100%;
  background: #ebf0ef;
  .main {
    text-align: left;
    background: #ebf0ef;
    .content {
      width: 92vw;
      background: rgba(255, 255, 255, 1);
      padding: 4vw;
      color: #333333;
      font-size: 1.4rem;
      margin-bottom: 2vh;
      word-break: break-all;
      .tt {
        margin-bottom: 15px;
      }
      .ct {
        text-indent: 2em;
        margin-bottom: 15px;
      }
      .ans {
        padding-left: 10px;
        .an {
          display: flex;
          margin-bottom: 10px;
        }
      }
    }

    .footer {
      display: flex;
      justify-content: space-around;
      padding: 2rem 0;
      button {
        background: rgba(0, 203, 171, 1);
        border-radius: 5px;
        font-size: 1.3rem;
        line-height: 3;
        color: #fff;
        padding: 0 1.5rem;
      }
    }
  }
}
.survey_info {
  display: flex;
  justify-content: center;

  width: 100vw;
  padding: 2vh 0;
  background: #fff;
  button {
    width: 50%;
    margin: 0 10vw;
  }
}
</style>


